<template>
  <a>
    <button class="largeButton" @click="scrollDown()">
      <h1>{{ buttonText }}</h1>
      <h1>
        <svg
          style="transform: scale(1.5)"
          xmlns="http://www.w3.org/2000/svg"
          height="100%"
          viewBox="0 0 24 24"
          width="24"
        >
          <path d="M0 0h24v24H0V0z" fill="none" />
          <path
            fill="var(--textLight)"
            d="M20 12l-1.41-1.41L13 16.17V4h-2v12.17l-5.58-5.59L4 12l8 8 8-8z"
          />
        </svg>
      </h1>
    </button>
  </a>
</template>

<script>
export default {
  props: ["buttonText"],
  methods: {
    scrollDown() {
      for (var i = 0; i < window.innerHeight; i++) {
        setTimeout(() => {
          window.scrollBy(0, 1);
        }, i * 0.5);
      }
    },
  },
};
</script>

<style scoped>
.largeButton {
  text-decoration: none;
  display: flex;
  border: none;
  outline: none;
  background-color: var(--accent);
  transition: var(--transition);
}
:hover.largeButton {
  background-color: var(--accentHover);
}
</style>