import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../pages/Home.vue";
import Logos from "../pages/Logos.vue";

Vue.use(VueRouter);
const routes = [
  {
    path: "/",
    name: "Home",
    component: Home,
  },
  {
    path: "/logos",
    name: "Logos",
    component: Logos,
  },
];

const router = new VueRouter({
  routes,
  scrollBehavior(to, from, savedPosition) {
    return { x: 0, y: 0 }
  },
});

export default router;
